import request from './request';

/**
 * 发布文章
 * @param {Object} obj
 */
async function addBlog(obj) {
  const result = await request().put('/api/blog', obj)
    .then(res => res.data)
    .catch(error => console.error('Error:', error));
  return result;
}
/**
   * 编辑文章
   * @param {Object} obj
   */
async function editBlog(obj) {
  const result = await request().post('/api/blog', obj)
    .then(res => res.data)
    .catch(error => console.error('Error:', error));
  return result;
}
/**
   * 发布每日一句
   * @param {Object} obj
   */
async function addEveryDay(obj) {
  const result = await request().put('/api/everyDay', obj)
    .then(res => res.data)
    .catch(error => console.error('Error:', error));
  return result;
}

export {
  addBlog,
  addEveryDay,
  editBlog,
};
